import React from "react";
import ExperienceCard from "../../components/experienceCard/ExperienceCard.js";
import "./ExperienceAccordion.css";
import { Accordion, Panel } from "baseui/accordion";
import { useLanguage } from "../../context/LanguageContext";

function ExperienceAccordion(props) {
  const { language } = useLanguage();
  const theme = props.theme;

  const getTranslatedTitle = (title) => {
    switch (title) {
      case "Work Experience":
        return language === "en" ? "Work Experience" : " ";
      case "Research Projects":
        return language === "en" ? "Research Projects" : " ";
      case "Leadership":
        return language === "en" ? "Leadership" : "";
      default:
        return title;
    }
  };

  const getTranslatedExperience = (experience) => {
    if (language === "en") return experience;

    const translations = {
      "Software Engineer (Firmware)": {
        title: "  ()",
        description:
          "             git   .\n UI  CORS         API .\n   UI    UI   85% .",
      },
      "Research Assistant": {
        title: " ",
        description:
          " Unity    VR     .\n    3D   3D   .\n  98%         .",
      },
      "Stock Market Price Prediction with ML": {
        title: "ML     ",
        description:
          " LSTM GRU       .\n        .\n ML         .",
      },
      "Reinforcement Learning from Visual Inputs": {
        title: "   ",
        description:
          " OpenAI Gym  Cartpole      RL   .\n Variational Auto-Encoder       .\n RL        .",
      },
      "GPU vs CPU Performance Analysis": {
        title: "GPU vs CPU  ",
        description:
          "    CPU GPU      .\n     IU BigRed 200  .\n   GPU vs CPU     .",
      },
      "Vice President / Team Leader": {
        title: " / ",
        description:
          " 3       .\n       .\n      .",
      },
      "Seminar Assistant": {
        title: " ",
        description:
          " 20         .\n      1,000    .",
      },
    };

    const translation = translations[experience.title];
    return translation ? { ...experience, ...translation } : experience;
  };

  return (
    <div className="experience-accord">
      <Accordion
        overrides={{
          Root: {
            style: {
              marginTop: "30px",
              marginBottom: "30px",
            },
          },
        }}
      >
        {props.sections.map((section) => (
          <Panel
            className="accord-panel"
            title={getTranslatedTitle(section.title)}
            key={section.title}
            overrides={{
              Header: {
                style: () => ({
                  backgroundColor: `${theme.body}`,
                  border: `2px solid ${theme.headerColor}`,
                  borderRadius: "10px",
                  marginBottom: "15px",
                  padding: "15px",
                  fontSize: "20px",
                  fontFamily: "Google Sans Regular",
                  color: `${theme.text}`,
                  cursor: "pointer",
                  ":hover": {
                    color: `${theme.secondaryText}`,
                    backgroundColor: `${theme.headerColor}20`,
                  },
                }),
              },
              Content: {
                style: () => ({
                  backgroundColor: `${theme.body}`,
                  padding: "20px 10px",
                }),
              },
              ToggleIcon: {
                style: () => ({
                  color: theme.text,
                }),
              },
            }}
          >
            {section.experiences.map((experience, index) => (
              <ExperienceCard
                key={`${section.title}-${index}`}
                index={index}
                totalCards={section.experiences.length}
                experience={getTranslatedExperience(experience)}
                theme={theme}
              />
            ))}
          </Panel>
        ))}
      </Accordion>
    </div>
  );
}

export default ExperienceAccordion;
